import React from "react"
import { Link, graphql } from "gatsby"
import { documentToReactComponents } from "@contentful/rich-text-react-renderer"

import {
  FaPaperPlane,
  FaBullseye,
  FaUserFriends,
  FaPaperclip,
  FaArrowAltCircleRight,
} from "react-icons/fa"
import Layout from "../components/layout"
import "../styles/bootstrap.min.css"
import Background from "../images/background.jpg"
import BackgroundAboutImage from "../images/backgroundblog.jpg"
import Head from "../components/Head"

export const query = graphql`
  query {
    allContentfulTeams {
      edges {
        node {
          title
          name
          description
          image {
            file {
              url
            }
          }
        }
      }
    }
    allContentfulAboutUs {
      edges {
        node {
          description {
            json
          }
          vision
          mission
          visionDescription
          missionDescription
          valueDescription
          value
          coreValue
          coreValueDescription
        }
      }
    }
    allContentfulBeneficiaries(limit: 1) {
      edges {
        node {
          title
          student
          idPs
          women
          indigent
          people
          image {
            title
            file {
              url
            }
          }
        }
      }
    }
  }
`

const About = props => {
  const data = props.data.allContentfulTeams
  const beneficiaries = props.data.allContentfulBeneficiaries
  const about = props.data.allContentfulAboutUs

  return (
    <Layout>
      <Head title="About" />
      <section
        className="page-title"
        style={{ backgroundImage: `url(${BackgroundAboutImage})` }}
      >
        <div className="auto-container">
          <div className="text-center">
            <h1>About</h1>
          </div>
        </div>
      </section>
      {about.edges.map(edge => {
        return (
          <div key={edge.node.vision}>
            <section
              className="welcome-section welcome-space"
              style={{ marginBottom: "1px", paddingBottom: "0" }}
            >
              <div className="auto-container">
                <div className="row clearfix">
                  <div className="col-md-7 col-sm-12 col-xs-12">
                    <div className="row">
                      <div
                        style={{
                          color: "black",
                          fontWeight: "400",
                          fontSize: "1.3rem",
                          paddingTop: "2rem",
                          lineHeight: "3rem",
                        }}
                      >
                        {edge.node.description &&
                          documentToReactComponents(edge.node.description.json)}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section
              className="Practice-space"
              style={{ paddingTop: "0", marginTop: "1px" }}
            >
              <div className="container">
                <div className="row">
                  <div className="col-sm-6 col-md-4">
                    <div className="practise-area">
                      <div className="thumb">
                        {/* <img src={TeamImage} alt="img" /> */}
                      </div>
                      <div className="practise-details">
                        <div className="round-style">
                          <i className="FaBullseye" style={{ marginLeft: "0" }}>
                            <FaBullseye
                              style={{
                                height: "60px",
                                color: `#3c7eb3`,
                                width: "40px",
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                        </div>

                        <h6 className="title">{edge.node.vision}</h6>
                        <p className="details">{edge.node.visionDescription}</p>
                      </div>
                    </div>
                  </div>
                  <div className="col-sm-6 col-md-4">
                    <div className="practise-area">
                      <div className="thumb"></div>
                      <div className="practise-details">
                        <div className="round-style">
                          <i
                            className="FaUserFriends"
                            style={{ marginLeft: "0" }}
                          >
                            <FaPaperPlane
                              style={{
                                height: "60px",
                                color: `#3c7eb3`,
                                width: "40px",
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                        </div>

                        <h6 className="title"> {edge.node.mission}</h6>
                        <p className="details">
                          {" "}
                          {edge.node.missionDescription}
                        </p>
                      </div>
                    </div>
                  </div>

                  <div className="col-sm-6 col-md-4">
                    <div className="practise-area">
                      <div className="thumb"></div>
                      <div className="practise-details">
                        <div className="round-style">
                          <i
                            className="FaUserFriends"
                            style={{ marginLeft: "0" }}
                          >
                            <FaUserFriends
                              style={{
                                height: "60px",
                                color: `#3c7eb3`,
                                width: "40px",
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                        </div>

                        <h6 className="title">{edge.node.value}</h6>
                        <p className="details">{edge.node.valueDescription}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section
              className="parallax-section"
              style={{ backgroundImage: `url(${Background})` }}
            >
              <div className="container">
                <div
                  className="text-center wow fadeInUp"
                  data-wow-delay="200ms"
                  data-wow-duration="1500ms"
                >
                  <h2
                    className="m-btm20"
                    style={{
                      textTransform: "uppercase",
                      textDecoration: "underline",
                      color: `#3c7eb3`,
                    }}
                  >
                    {edge.node.coreValue}
                  </h2>
                  <p
                    className="fs-16"
                    style={{
                      color: "black",
                      fontWeight: "400",
                      lineHeight: "35px",
                    }}
                  >
                    {edge.node.coreValueDescription}
                  </p>
                </div>
              </div>
            </section>
          </div>
        )
      })}
      <section
        className="welcome-section welcome-space"
        style={{ marginBottom: "1px", paddingBottom: "0" }}
      >
        <div className="auto-container">
          <div className="row clearfix">
            {/* <!--Column--> */}
            {beneficiaries.edges.map(edge => {
              return (
                <div
                  className="col-md-5 col-sm-12 col-xs-12 sm-text-center mb-4 thumb m-top30"
                  key={edge.node.title}
                >
                  <img
                    src={edge.node.image.file.url}
                    alt={edge.node.image.title}
                    style={{ height: "19rem", paddingTop: "0" }}
                  />
                </div>
              )
            })}

            {beneficiaries.edges.map(edge => {
              return (
                <div
                  className="col-md-7 col-sm-12 col-xs-12 mt-4"
                  key={edge.node.title}
                >
                  <div className="row" style={{ textAlign: "center" }}>
                    <h3 style={{ color: `#3c7eb3` }}>{edge.node.title}</h3>
                  </div>
                  <div className="col-md-7 col-sm-12 col-xs-12">
                    <ul
                      style={{
                        paddingLeft: "20px",
                        width: "700px",
                      }}
                    >
                      <li>
                        <p style={{ fontSize: "20px" }}>
                          {" "}
                          <i
                            className="FaPaperclip"
                            style={{ paddingRight: "7px" }}
                          >
                            <FaPaperclip
                              style={{
                                width: "50px",
                                height: "30px",
                                color: `#3c7eb3`,
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                          {edge.node.student}
                        </p>
                      </li>

                      <li>
                        <p style={{ fontSize: "20px" }}>
                          {" "}
                          <i
                            className="FaPaperclip"
                            style={{ paddingRight: "7px" }}
                          >
                            <FaPaperclip
                              style={{
                                width: "50px",
                                height: "30px",
                                color: `#3c7eb3`,
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                          {edge.node.indigent}
                        </p>
                      </li>
                      <li>
                        <p style={{ fontSize: "20px" }}>
                          {" "}
                          <i
                            className="FaPaperclip"
                            style={{ paddingRight: "7px" }}
                          >
                            <FaPaperclip
                              style={{
                                width: "50px",
                                height: "30px",
                                color: `#3c7eb3`,
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                          {edge.node.idPs}
                        </p>
                      </li>
                      <li>
                        <p style={{ fontSize: "20px" }}>
                          {" "}
                          <i
                            className="FaPaperclip"
                            style={{ paddingRight: "7px" }}
                          >
                            <FaPaperclip
                              style={{
                                width: "50px",
                                height: "30px",
                                color: `#3c7eb3`,
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                          {edge.node.women}
                        </p>
                      </li>
                      <li>
                        <p style={{ fontSize: "20px" }}>
                          {" "}
                          <i
                            className="FaPaperclip"
                            style={{ paddingRight: "7px" }}
                          >
                            <FaPaperclip
                              style={{
                                width: "50px",
                                height: "30px",
                                color: `#3c7eb3`,
                                paddingBottom: "3px",
                                paddingLeft: "2px",
                              }}
                            />
                          </i>
                          {edge.node.people}
                        </p>
                      </li>
                    </ul>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </section>

      {/* <!--Team Members Section--> */}
      <section className="team-section">
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center mb-3">
              <h1>Our Expert</h1>
            </div>
          </div>
          <div className="row clearfix">
            {/* <!--Column--> */}
            {data.edges.map(edge => {
              return (
                <div
                  className="col-md-6 col-sm-6 col-xs-12"
                  key={edge.node.title}
                >
                  <article className="team-area">
                    <div className="row clearfix">
                      <figure className="col-lg-6 col-md-12 image">
                        <Link to={`/`}>
                          <img
                            src={edge.node.image.file.url}
                            alt={edge.node.image.title}
                          />
                        </Link>
                      </figure>
                      <div className="col-lg-6 col-md-12">
                        <div className="content">
                          <div className="title-box">
                            <h4>{edge.node.name}</h4>
                            <p>{edge.node.title}</p>
                          </div>
                          <div className="text">
                            <p>{edge.node.description}</p>
                          </div>
                          <Link
                            to={edge.node.name}
                            style={{
                              boxShadow: `none`,
                              textDecoration: `none`,
                              color: `inherit`,
                              marginBottom: `10px`,
                            }}
                            className="btn-thm btn-xs"
                          >
                            More information
                            <i className="FaArrowAltCircleRight">
                              <FaArrowAltCircleRight />
                            </i>
                          </Link>
                        </div>
                      </div>
                    </div>
                  </article>
                </div>
              )
            })}
          </div>
        </div>
      </section>
    </Layout>
  )
}
export default About
